<template>
  <div>
    <p></p>
    <v-button @click="visible = true"></v-button>
    <v-button @click="visible3 = true"></v-button>

    <v-dialog v-model="visible" title="">
      <span></span>
      <span slot="footer">
        <v-button @click="visible = false"> </v-button>
        <v-button type="primary" @click="visible = false"> </v-button>
      </span>
    </v-dialog>

    <v-dialog v-model="visible3" title="" :closable="false">
      <span></span>
      <span slot="footer">
        <v-button @click="visible3 = false"> </v-button>
        <v-button type="primary" @click="visible3 = false"> </v-button>
      </span>
    </v-dialog>

    <p> top</p>
    <v-button @click="visible6 = true">top  10%</v-button>
    <v-button @click="visible7 = true">top  10px</v-button>

    <v-dialog v-model="visible6" title="" top="10%">
      <span></span>
      <span slot="footer">
        <v-button @click="visible6 = false"> </v-button>
        <v-button type="primary" @click="visible6 = false"> </v-button>
      </span>
    </v-dialog>

    <v-dialog v-model="visible7" title="" top="10px">
      <span></span>
      <span slot="footer">
        <v-button @click="visible7 = false"> </v-button>
        <v-button type="primary" @click="visible7 = false"> </v-button>
      </span>
    </v-dialog>

    <p></p>
    <v-button @click="visible4 = true"> 400px</v-button>
    <v-button @click="visible5 = true"> 400px</v-button>

    <v-dialog v-model="visible4" title="" width="400">
      <span></span>
      <span slot="footer">
        <v-button @click="visible4 = false"> </v-button>
        <v-button type="primary" @click="visible4 = false"> </v-button>
      </span>
    </v-dialog>

    <v-dialog v-model="visible5" title="" min-width="400">
      <span></span>
      <span slot="footer">
        <v-button @click="visible5 = false"> </v-button>
        <v-button type="primary" @click="visible5 = false"> </v-button>
      </span>
    </v-dialog>

    <p></p>
    <v-button @click="visible2 = true"></v-button>

    <v-dialog v-model="visible2" title="">
      <div slot="body" style="padding: 30px 0; text-align: center;">
        <span></span>
        <div style="margin-top: 20px;">
          <v-button type="success"></v-button>
          <v-button type="warning"></v-button>
          <v-button type="danger"></v-button>
          <v-button type="info"></v-button>
        </div>
      </div>
    </v-dialog>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        visible: false,
        visible2: false,
        visible3: false,
        visible4: false,
        visible5: false,
        visible6: false,
        visible7: false
      };
    }
  };
</script>

<style lang="scss" scoped>
  button {
    margin: 2px 5px;
  }
</style>


